.index{
    width: 100vw;
    height: 100vh;
    background-image: radial-gradient(#021027, #000000);
}
.backgroundImg{
    display: block;
    width: 100%;
    height: 100%;
}
.indexPosition{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;left: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.indexContent{
    width: 80%;
    height: 80%;
    background-color: #1a1a1a;
}